body {
    overflow-y: scroll !important;
}

.pvefx {
    color: var(--green);
}

.nvefx {
    color: var(--red);
}

.date {
    position: absolute;
    top: 8px;
    right: 16px;
}

.categories {
    font-weight: 700;
    text-decoration: underline;
}

.strain {
    text-transform: uppercase;
}

.modal {
    color: var(--dark);
}

.modal-delete-btn {
    background-color: var(--red);
    padding: 12px 12px;
    width: 80px;
    font-size: 1rem;
}

.pointer {
    cursor: pointer;
}

@media only screen and (max-width : 767.5px) {
    .larger-device {
        display: none;
    }

    h5 {
        margin-top: 10px;
    }
}

@media only screen and (min-width : 768px) {
    .smaller-device {
        display: none;
    }
}